
@import '../../node_modules/i-rubik/dist/rubik.min.css';
.translate {
  width: 80%;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h1 {
  text-align: center;
  margin: 50px 0;
}
.trans-file .upload {
  width: 350px;
}
.trans-input {
  overflow: hidden;
  margin: 20px 0;
}
.trans-input .upload {
  float: left;
  width: 350px;
  margin-right: 20px;
}
.trans-input .input {
  display: inline-block;
  margin-right: 20px;
  width: 150px;
}
.trans-input .btn-translate {
  margin-top: 1.5em;
}
.source-info {
  font-size: 15px;
}
.source-info .name {
  color: #67a6d3;
}
.source-info .position {
  color: #f60;
}
.source-info .source {
  color: #3c50e7;
}
.source-code {
  padding: 20px 40px;
  background: #e1e1e1;
}
.source-code li {
  position: relative;
  list-style: none;
  height: 20px;
  line-height: 20px;
  text-indent: 20px;
  font-size: 14px;
  color: #2196f3;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
}
.source-code li:nth-child(2n) {
  background: #ddd;
}
.source-code li:after {
  content: attr(data-no);
  position: absolute;
  left: -50px;
  top: 0;
  color: #39b0f7;
}


